@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;500;700&display=swap');
.App {
  text-align: center;
  display: flex;
  /* justify-content: center; */
  align-items: center;
  height: 100vh;
  flex-direction: column;
  background-color: #111;
  color: aliceblue;

  position: relative;
}
.projectinfo{
  position: absolute;
  display: flex;
  align-items: flex-end;

  right: 10px;
  bottom: 7.5%;
  font-size: 2rem;
  opacity: .4;
  width: 52px;
  height: 45px;
  border-top-left-radius: 20px;
  border-bottom-left-radius: 20px;
  background-color: grey;


}
.projectinfo a{
  text-decoration: none;
  color: white;

  padding-left: 5px;
}
.projectinfo:hover{
  opacity: 1;
  width: 100px;
  transition: .4s all ease;
  justify-content: flex-start;


}

.App h2 {
  font-size: 2.4rem;
  font-weight: 500;
  font-family: 'Montserrat', sans-serif;
  letter-spacing: 2px;
  margin-bottom: -15px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.heads{
  display: flex;
  flex-direction: row;
}
.App h2::after{
  content: "";
  width: 150px;
  height: 4px;
  background: deeppink;
  border-radius: 20px;
  margin-top: 3px;
}
.App h2:hover::after{
  
  animation: underline .3s linear;
}

@keyframes underline {
  0% {
    width: 200px;
  }
  100% {
    width: 150px;
  }
}

.t2i {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 80vh;
  width: 80vw;
  /* border: 2px solid red; */
  position: relative;
}

.inputText {
  width: 40vw;
  border: 3px solid rgb(26, 16, 99);

  display: flex;
  justify-content: center;
  align-items: center;
  height: 35px;
  background-color: black;
  border-radius: 10px;
  position: absolute;
  bottom: 30px;


}

.inputText:focus-within {
  border: 3px solid white;
  transition: .3s all ease;
}
.downloadBtn{
  position: absolute;
  right: 30px;
  bottom: -28px;

  display: flex;
  justify-content: center;
  align-items: center;

  width: 50px;
  height: 50px;
  background-color: black;
  border-radius: 50%;
  border: 4px solid rgb(255, 0, 0);;
}
.downloadBtn .down{
  font-size: 1.8rem;
  cursor: pointer;
  color: lime;
}
.downloadBtn .down:hover, .downloadBtn:hover{
  transform: scale(1.1);
  transition: .3s all ease;
}


.inputText input {

  width: 35vw;

  font-size: 1rem;
  background-color: transparent;
  border: none;
  color: white;
}

.inputText input:focus {
  border: none;
  outline: none;
}

.inputText input::placeholder {
  color: aliceblue;
}

.inputText button {
  height: 35px;
  background-color: transparent;
  border: none;
  cursor: pointer;
  font-size: 1.8rem;
}

.inputText button svg {
  display: flex;
  align-items: center;
  justify-content: center;
  color: yellowgreen;

}

.inputText button svg:hover {
  color: lime;
  transition: .3s all ease;
}

@media(max-width: 951px) {
  .inputText {
    width: 80vw;
  }

  .inputText input {
    width: 70vw;
  }
}

@media(max-width: 551px) {
  .inputText {
    width: 90vw;
    padding: 5px;
  }

  .inputText input {
    width: 84vw;
  }
}


.outputMain {
  width: 300px;
  height: 300px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: aliceblue;
  padding: 10px;
  position: relative;
  /* overflow: hidden; */

  border: 4px solid rgb(255, 0, 0);
  border-radius: 15px;
  

}

.outputMain img {
  max-width: 100%;
  max-height: 100%;
  object-fit: contain;
  border-radius: 10px;
}

@media (max-width: 768px) {
  .t2i{
    padding-left: 5%;
    padding-right: 5%;
    position: relative;
  }
  .outputMain{
    position: absolute;
    width: 80vw;
  }
}

@media (max-width: 480px) {
  .projectinfo{
    font-size: 1.6rem;
    height: 38px;
  }
}
.footer a{
  text-decoration: none;
  color: crimson;
}
